.logo-container {
    display: flex;
    align-items: center;
    justify-content: center;
}

.logo-container .logo {
    font-size: 30px;
    font-weight: bold;
    padding: 2px 10px 2px 0;
    color: yellow;
    background: transparent;
    border-bottom: none;
    text-shadow: 5px 8px 4px rgba(118, 242, 238, 0.5);
    transition: 0.5s all ease-in-out;
}

.logo:hover {
    color: aqua;
    transform: scale(1.1);
}


.logo-container .logo.dark-mode {
    color: rgb(83, 191, 245);
    text-shadow: 5px 8px 4px rgba(209, 210, 210, 0.5);
    transition: 0.5s all ease-in-out;
    border-bottom: none;
}

.logo-container .logo.dark-mode:hover {
    color: rgb(31, 243, 66);
    transition: 0.5s all ease-in-out;
}


@media (max-width: 700px) {
    .logo-container .logo {
        font-size: 20px;
        margin-left: 10px;
        color: yellow;
        background: transparent;
    }

    .logo-container .logo.dark-mode {
        background: transparent;
    }
}