
.footer {
  background-color: white;
    border-top: 2px solid lightgray;
    padding: 1px 0 10px 0;
    position: relative;
    z-index: 200;
  }
  
  .footer.dark-mode {
  background-color: black;
  border-top: 2px solid aqua;
  }
  
  .fp p.dark-mode{
  color: white;
  }
  
  .footer-content {
    display: flex;
    justify-content: space-around;
    align-items: center;
    border-bottom: 2px solid lightgray;
    padding: 20px;
  }
  
  .footer-content.dark-mode{
    border-bottom: 2px solid aqua;
  }
  
  .footer-content.dark-mode {
    background-color: black;
  }
  
  .logo-container1 {
    display: flex;
    align-items: center;
    padding: 5px;
  }
  
  .logo-container1.dark-mode {
   background-color: black;
  }
  
  .flogo {
    font-size: 30px;
    font-weight: bold;
    margin-right: 10px;
    color: purple;
    border-bottom: none;
    text-shadow: 5px 8px 4px rgba(145, 148, 148, 0.5);
    transition: 0.5s all ease-in-out;
  }
  
  .flogo.dark-mode{
    background-color: black;
    color: rgb(83, 191, 245);
    text-shadow: 5px 8px 4px rgba(209, 210, 210, 0.5);
    transition: 0.5s all ease-in-out;
  }
  
  .flogo:hover {
    color: rgb(7, 192, 71);
    transform: scale(1.1);
  }
  
  .footer-social a {
    color: black;
    text-decoration: none;
    font-size: 20px;
    margin-right: 15px;
  }
  
  .footer-social.dark-mode{
    background-color: black;
  }
  .footer-social a i.dark-mode {
    color: white;
    background-color: black;
  }
  
  .footer-content2{
    display: flex;
    justify-content: space-around;
    gap: 40px;
  }
  
  .footer-content2.dark-mode{
     background-color: black;
  }
  
  h4{
    margin-bottom: 30px;
    margin-top: 0;
  }
  
  h4.dark-mode{
    color: white;
  }
  
  .footer-content2 .a{
    color: black;
  }
  
  .footer-content2 a.dark-mode{
    color: white;
    background-color: black;
  }
  
  .footer-content2 a:hover{
    text-decoration: underline;
  }
  
  .footer-content2 a button{
    margin-left: 5px;
    padding: 2px 5px;
    font-size: 12px;
    font-weight: bold;
    color: white;
    background-color: rgb(86, 238, 86);
    border-radius: 5px;
    border: none;
  }
  
  .footer-bottom {
    margin-top: 10px;
    text-align: center;
  }
  
  .footer-bottom.dark-mode {
   background-color: black;
  }
  
  .footer-bottom p.dark-mode {
    color: white;
   }
  
   
@media (max-width: 700px) {
  
    .footer-content {
      flex-direction: column;
      text-align: center;
    }
  
    .footer-content1{
      margin: 50px;
      margin-bottom: 20px;
      border-bottom: 2px solid lightgray;
    }
    .footer-content1.dark-mode{
      border-bottom: 2px solid rgb(230, 31, 230);
    }
   
  
    .footer-social {
      margin: 15px 0 30px;
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
    }
  
    .footer-content2{
      flex-direction: column;
    }
  
    .discover, .about{
      border-bottom: 2px solid lightgray;
    }
  
    .discover.dark-mode, .about.dark-mode{
      border-bottom: 2px solid rgb(230, 31, 230);;
    }
  }
  