.signInContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  height: 95vh;
  position: relative;
}

.signInContainer.dark-mode {
background-color: black;
}

.formContainer {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
  padding: 10px;
  width: 100%;
  max-width: 600px;
  height: auto;
}

.formContainer.dark-mode {
  background-color: black;
  color: white;
  box-shadow: 0 0 20px aqua;
  padding: 10px;
  width: 100%;
  max-width: 600px;
}

.heading {
  text-align: center;
}

 .formsignin, .formbottom, .signdiv, .form{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
}

.form{
  margin: 20px;
}

.hr{
  color: white;
  width: 100%;
  border-radius: 50px;
}

.formcontent{
  display: flex;
  justify-content: space-between;
}

.register1{
  padding-bottom: 0;
}

.fielddiv1 {
  margin: 0 0 0 0;
  padding: 0;
  width: calc(72% - 20px);
}

.fielddiv1 .emaillabel p, .fielddiv1 .pswlabel p{
  text-align: left;
  font-size: .9rem;
}

.fielddiv1 .emaillabel input, .fielddiv1 .pswlabel input{
   font-size: .9rem;
}

.fielddiv {
  margin: 0;
  padding: 0;
  width: calc(50% - 20px);
}

label {
  display: block;
  font-weight: normal;
}

.required{
  color: white;
}

input {
  width: 100%;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
}

input.dark-mode {
  border: 1px solid #ccc;
  background-color: darkgray;
  font-size: 16px;
}

.placeholder-text::placeholder {
  font-size: small;
  opacity: .5;
}

.placeholder-text.dark-mode::placeholder{
  font-size: small;
  opacity: .5;
  color: white;
}

.sendotpdiv{
  text-align: center;
  margin-top: 10px;
}

.sendOtp{
  background: rgb(231, 229, 229);
  color: black;
  font-size: .7rem;
  font-weight: normal;
  margin: 0;
  padding: 5px 5px;
  border-radius: 5px;
  border: 1px solid black;
  box-shadow: 0 0 5px rgb(62, 62, 62);
}

.sendOtp:hover{
  background: rgb(231, 229, 229);
  color: black;
  font-size: .7rem;
  font-weight: normal;
  margin: 0;
  padding: 5px 5px;
  border-radius: 5px;
  border: 1px solid black;
  box-shadow: 0 0 5px rgb(62, 62, 62);
}

.verifyContainer{
  margin-top: -10px;
}

.verifyContainer p{
  text-align: left;
}

.verifydiv{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: -10px;
}

.verifydiv input{
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
  width: 60%;
}

.verifydiv .verifyOtp{
  background: rgb(231, 229, 229);
  color: black;
  font-size: .8rem;
  font-weight: normal;
  padding: 5px 5px;
  border-radius: 5px;
  border: 1px solid rgb(104, 104, 104);
  box-shadow: 0 0 5px rgb(62, 62, 62);
}

.emaillabel {
  position: relative;
}

.emaillabel.verified::after {
  content: "\f058";
  font-family: 'Font Awesome 5 Free';
  font-weight: 900;
  font-size: 14px;
  color: #00cc00;
  position: absolute;
  top: 62%;
  right: 10px;
  transform: translateY(-50%);
}

.error {
  color: red;
  font-size: 14px;
}

.sweet-loading {
 display: flex;
 flex-direction: column;
 justify-content: center;
 align-items: center;
}

.sweet-loading .loadingp{
  color: black;
  font-size: 1.5rem;
  font-weight: bold;
}

.sweet-loading .loadingp.dark-mode{
  color: white;
}

.loadingSendOtp{
  display: flex;
  justify-content: center;
  align-items: center;
}

.loadingSendOtp .otpsendp{
  font-size: .9rem;
}

.signInContainer.loading-opacity {
  opacity: 0.5;
  pointer-events: none;
}

.signdiv, .signupdiv{
  text-align: center;
  width: 100%;
  margin: 25px 0 0 0;
}

.sign1, .signup {
  text-align: center;
  background-color: #4caf50;
  color: #fff;
  padding: 10px 50px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s;
  width: 35%;
  margin: 0;
  border: 2px solid white;
}

.sign1:disabled, .signup:disabled {
  background-color: #4caf50;
  opacity: .6;
  transition: none;
  transform: none;
  border: none;
  box-shadow: none;
  cursor: not-allowed;
}

.sign1:hover:enabled, .signup:hover:enabled {
  background-color: #45a049;
  border: none;
  box-shadow: none;
  border: 2px solid white;
}

.google{
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
}

.google .img {
  background-color: transparent;
  height: 20px;
  width: 20px;
  margin-left: 10px;
  cursor: pointer;
  font-size: 16px;
  padding: 1px;
  border: 1px solid lightgray;
  border-radius: 50%;
}

.fbimg{
  background-color: transparent;
  height: 20px;
  width: 20px;
  margin-left: 10px;
  cursor: pointer;
  font-size: 16px;
  border: 1px solid lightgray;
  border-radius: 50%;
}

.google .img:hover {
  transform: scale(1.2);
  transition: all .5s ease-in-out;
}

.register {
  text-align: center;
  margin: 0;
  padding: 0;
}

.register p{
  margin: 0;
  padding: 0;
  position: relative;
  top: 5px;
}

.register button {
  background-color: transparent;
  color: blue;
  border: none;
  box-shadow: none;
  cursor: pointer;
  font-size: 14px;
  text-decoration: underline;
}

.register button:hover {
  text-decoration: none;
}

.required{
  color: red;
}


@media (max-width: 700px) {
  .formContainer {
    padding: 10px;
    max-width: 300px;
    height: auto;
  }

  .formContainer.dark-mode {
    max-width: 350px;
  }
  
  .heading {
    font-size: .9rem;
    width: 100%;
  }
  
  .formcontent{
    display: flex;
    justify-content: space-between;
    height: 90px;
  }
  
  .fielddiv1{
    margin: 0 0 10px 0;
  }

  label {
    display: block;
    font-weight: normal;
    font-size: .7rem;
  }
  
  input {
    padding: 4px;
  }

  .placeholder-text::placeholder {
    font-size: .7rem;
  }
  
  .placeholder-text.dark-mode::placeholder{
    font-size: .7rem;
    opacity: .5;
    color: white;
  }

  .error {
    margin: -12px 0;
    font-size: .6rem;
    display: block;
  }

 .psw{
  position: relative;
  top: 10px;
  }

  .sendotpdiv{
    text-align: center;
    position: relative;
    bottom: 4px;
  }
  
  .verifyContainer{
    margin-top: -5px;
  }

  .verifyContainer p{
    font-size: .6rem;
    margin-bottom: 12px;
  }
  
  .verifydiv{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: -10px;
  }
  
  .verifydiv input{
    padding: 4px;
    border: 1px solid #ccc;
    border-radius: 4px 0 0 4px;
    font-size: .7rem;
    width: 100%;
  }
  
  .verifydiv .verifyOtp{
    background: rgb(231, 229, 229);
    color: black;
    font-size: .6rem;
    font-weight: normal;
    padding: 4.2px 3px;
    border-radius: 0 4px 4px 0;
  }
  
  .signdiv{
    margin: 40px 0 15px 0;
  }

  .signupdiv{
    margin: 15px 0 10px 0;
  }
  
  
  .sign1, .signup {
    padding: 5px 0px;
    font-size: 14px;
  }
  
  
.google{
  margin: 0 0 15px 0;
  font-size: .8rem;
}

.google .img {
  height: 15px;
  width: 15px;
  cursor: pointer;
}

.google .img:hover {
  transform: scale(1.2);
  transition: all .5s ease-in-out;
}

.hr{
  margin: 20px;
}

.register {
  text-align: center;
}

.register p{
  margin: 0;
  padding: 0;
  position: relative;
  top: -10px;
  font-size: .8rem;
}

.register button {
  font-size: .7rem;
  text-decoration: underline;
}

.error{
  margin: 5px 0;
}

}
