.about-container {
  display: flex;
  position: relative;
  padding-top: 80px;
}

.about-container.dark-mode {
  background-color: black;
}

.about-side-nav {
  padding: 80px 30px 0;
  background-color: #f8f8f8;
  border-right: 2px solid darkgray;
  position: fixed;
  top: 0;
  height: 100%;
  overflow-y: auto;
  z-index: 1;
}

.about-side-nav.dark-mode {
  background-color: black;
  border-right: 2px solid aqua;
  transition: 0.5s all ease-in-out;
}

.about-side-nav ul {
  list-style-type: none;
  padding: 0;
}

.aboutd {
  margin-bottom: 20px;
  border-bottom: 2px solid darkgray;
}

.aboutd.dark-mode {
  color: white;
  border-bottom: 2px solid rgb(228, 29, 228);
  transition: 0.5s all ease-in-out;
}

.about-side-nav li {
  margin: 0 0 15px 30px;
}

.about-side-nav .link {
  text-decoration: none;
  color: rgb(143, 141, 141);
  font-size: .9em;
}

.about-side-nav h1.dark-mode,
.link.dark-mode {
  color: rgb(175, 175, 175);
  transition: 0.5s all ease-in-out;
}

.about-side-nav .link:hover,
.about-side-nav .link.active {
  color: black;
  font-weight: bold;
  transition: 0.5s all ease-in-out;
}

.about-side-nav .link:hover.dark-mode,
.about-side-nav .link.active.dark-mode {
  color: white;
  font-weight: bold;
  transition: 0.5s all ease-in-out;
}

.about-page {
  flex: 1;
  max-width: calc(100% - 20px);
  /* Adjust the width to leave space for the fixed sidebar */
  margin-left: 260px;
  padding: 20px;
  font-family: 'Arial', sans-serif;
  line-height: 1.6;
  overflow-y: auto;
}

.about-page .hr{
  border: 1px solid rgb(246, 96, 246);
  border-radius: 20px;
  margin: 20px;
}

.about-page .hr.dark-mode{
  border: 1px solid rgb(246, 96, 246);
  border-radius: 20px;
  margin: 20px;
}

.about-page-content {
 display: flex;
 flex-direction: column;
 justify-content: center;
 align-items: center;
}

.about-page.dark-mode {
  background-color: black;
  color: white;
  transition: 0.5s all ease-in-out;
}

.about-page-content.active{
  border-radius: 20px;
  border: 2px solid aqua;
  box-shadow: 0 0 20px rgb(228, 29, 228);
  background-color: lightgray;
  transition: 0.5s all ease-in-out;
}

.about-page-content.active.dark-mode{
  border-radius: 20px;
  border: 2px solid aqua;
  box-shadow: 0 0 20px rgb(228, 29, 228);
  background-color: rgb(113, 112, 112);
  transition: 0.5s all ease-in-out;
}

.about-page-content.dark-mode {
  background-color: black;
  color: white;
}

.about-page h1 {
  transition: 0.5s all ease-in-out;
}

.about-page-content h1.dark-mode {
  color: white;
  transition: 0.5s all ease-in-out;
}

.about-page-content p {
  margin-left: 25px;
}


@media only screen and (max-width: 700px) {
  .about-container {
    display: flex;
    flex-direction: column;
  }

  .about-side-nav {
    padding: 80px 10px 0;
    background-color: #f8f8f8;
    border-right: 2px solid darkgray;
    position: fixed;
    top: 0;
    height: 100%;
    overflow-y: auto;
    z-index: 1;
  }

  .about-side-nav ul {
    width: 20vw;
    margin: 0;
    padding: 0;
  }

  .about-side-nav ul .aboutd h1{
    font-size: .8rem;
  }

  .about-side-nav ul li{
    font-size: .7rem;
    margin: 20px 0;
    text-align: center;
  }
  
  .about-page {
    flex: 1;
    max-width: calc(100%-20vw);
    margin: 0 0 0 20%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .about-page-content {
    font-size: smaller;
    width: 80%;
    margin: 0;
  }

  .about-page-content h1 {
    font-size: large;
  }

  .about-page-content h1, h2, p {
    text-align: center;
  }
}