.welcome-container {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(black, black);
}

.welcome-message {
    text-align: center;
    padding: 20px;
    background-color: rgb(40, 39, 39);
    color: white;
    border-radius: 10px;
    box-shadow: 0 0 20px aqua
}

.welcome-message h1:nth-of-type(2) {
    animation: musicWave 1s infinite;
}

.app-name {
    font-size: 2em;
    color: #3498db;
    margin-bottom: 10px;
    animation: changeColor1 2s infinite alternate;
}

.created-by {
    font-size: 2em;
    color: rgb(250, 131, 250);
    margin-bottom: 10px;
    animation: changeColor2 2s infinite alternate;
}

.circle-animation {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin: 0 auto;
    margin-top: 20px;
    background-color: #2ecc71;
    animation: bounce 1s infinite, changeColor3 2s infinite alternate;
}

@keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
        transform: translateY(0);
    }
    40% {
        transform: translateY(-20px);
    }
    60% {
        transform: translateY(-10px);
    }
}

@keyframes changeColor1 {
    0% {
        color: #3498db;
    }
    25% {
        color: #e74c3c;
    }
    50% {
        color: #2ecc71;
    }
    75% {
        color: #f39c12;
    }
    100% {
        color: #3498db;
    }
}

@keyframes changeColor2 {
    0% {
        color: rgb(249, 84, 249);
    }
    25% {
        color: #2ecc71;
    }
    50% {
        color: #f39c12;
    }
    75% {
        color: aqua;
    }
    100% {
        color: rgb(249, 84, 249);
    }
}

/* WelcomeMessage.css */
@keyframes musicWave {
    0%, 100% {
        transform: translateY(0);
    }
    25% {
        transform: translateY(-5px);
    }
    50% {
        transform: translateY(5px);
    }
    75% {
        transform: translateY(-3px);
    }
}


@keyframes changeColor3 {
    0% {
        background-color: #2ecc71;
    }
    25% {
        background-color: #f39c12;
    }
    50% {
        background-color: #3498db;
    }
    75% {
        background-color: #e74c3c;
    }
    100% {
        background-color: #2ecc71;
    }
}

@media (max-width: 700px) {
    .welcome-container {
        width: 100%;
        height: 100vh;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #f2f2f2;
    }
}