.contact-container1 {
  height: 90vh;
  width: 100%;
  padding: 60px 10px 10px;
  border-bottom: 2px solid lightgray;
  display: flex;
  justify-content: center;
  align-items: center;
}

.contact-container1.dark-mode {
  background-color: black;
  border-bottom: 1px solid aqua;
}

.contact-container {
  width: 400px;
  margin: 50px auto;
  padding: 20px;
  box-shadow: 0 0 50px purple;
  position: relative;
  border-radius: 20px;
}

.contact-container.dark-mode {
  background-color: rgb(70, 70, 70);
  box-shadow: 0 0 50px aqua;
}

.contact-container h2 {
  text-align: center;
}

.contact-container h2.dark-mode,
.contact-container form label.dark-mode {
  color: white;
}

.contact-container form {
  display: flex;
  flex-direction: column;
  gap: 10px;
  border-top: 2px solid rgb(237, 34, 237);
  padding: 20px;
}

.contact-container form input::placeholder, .contact-container form textarea::placeholder{
  font-size: .8rem;
  opacity: .4;
}

.required {
  color: red;
}

.contact-container form input,
.contact-container form textarea {
  width: 100%;
  padding: 8px;
  box-sizing: border-box;
  border-radius: 10px;
  border: 2px solid rgb(140, 142, 141);
  margin-bottom: 15px;
  box-sizing: border-box;
}

.contact-container form button {
  padding: 10px;
  margin-top: 10px;
  background-color: #dd1af7;
  color: white;
  font-weight: bolder;
  font-size: medium;
  border-radius: 10px;
  border: 2px solid rgb(0, 187, 255);
  cursor: pointer;
}

.contact-container form button:hover {
  color: white;
  background-color: #c30edb;
  transform: scale(1.1);
  transition: 0.5s all ease-in-out;
  box-shadow: 0 0 20px aqua;
}


.contact-container .thank-you-message {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
  background-color: #28a745;
  color: #fff;
  padding: 40px 30px;
  border-radius: 10px;
  border: 2px solid aqua;
  box-shadow: 0 0 90px rgba(0, 0, 0, 0.9);
  text-align: center;
  animation: fadeOut 5s ease-in-out;
}

@keyframes fadeOut {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

.contact-container form .error-message {
  color: red;
  font-size: 14px;
  margin-top: 5px;
}


@media only screen and (max-width: 700px) {
  .contact-container {
    max-width: 250px;
    margin: 80px auto;
  }
}