.services-container {
  display: flex;
  padding-top: 60px;
}

.services-container.dark-mode {
  color: white;
  background-color: black;
}

.sidebar {
  flex: 0 0 300px;
  padding: 68px 30px 0;
  margin-top: 0;
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  overflow-y: auto;
  border-right: 2px solid darkgrey;
}

.sidebar.dark-mode {
  background-color: black;
  color: white;
  border-right: 2px solid aqua;
  transition: all 0.5s ease-in-out;
}

.sidebar ul{
  border-top: 2px solid rgb(228, 29, 228);
  padding-top: 20px;
}

.sidebar ul li{
  list-style: none;
  margin: 40px 0;
  color: rgb(127, 126, 126);
}

.sidebar ul li.dark-mode{
  color: rgb(195, 194, 194);
  transition: all 0.5s ease-in-out;
}

.sidebar ul li:hover{
  color: black;
  transform: scale(1.1);
 transition: all 0.5s ease-in-out;
}

.sidebar ul li.active{
 color: black;
 transition: all 0.5s ease-in-out;
}

.sidebar ul li:hover.dark-mode{
  color: white;
  transform: scale(1.1);
  transition: all 0.5s ease-in-out;
}

.sidebar ul li.active.dark-mode{
 color: white;
 transition: all 0.5s ease-in-out;
}

.services-div{
  margin-bottom: 40px;
  border-bottom: 3px solid rgb(228, 29, 228);
}

.services-header.dark-mode{
  color: white;
  transition: all 0.5s ease-in-out;
}

.content-container {
  flex: 1;
  padding: 20px;
  margin-left: 320px; 
  overflow-y: auto;
}

.content-container.dark-mode {
  background-color: black;
  color: white;
  transition: all 0.5s ease-in-out;
}

.general-section,
.license-section,
.terms-container {
  margin-bottom: 50px;
  border-bottom: 3px solid lightgray;
  opacity: 1;
}

.general-section.dark-mode,
.license-section.dark-mode,
.terms-container.dark-mode {
  color: white;
  border-bottom: 3px solid rgb(228, 29, 228);
  transition: all 0.5s ease-in-out;
}

.general-section.active,
.license-section.active,
.terms-container.active {
  background-color: lightgray;
  border-radius: 20px;
  border: 2px solid aqua;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.452);
  padding: 15px;
  opacity: 1;
  transition: all 0.5s ease-in-out;
}

.general-section.active.dark-mode,
.license-section.active.dark-mode,
.terms-container.active.dark-mode {
  background-color: rgb(98, 98, 98);
  border: 2px solid aqua;
  box-shadow: 0 0 30px rgb(228, 29, 228);
  opacity: 1;
  transition: all 0.5s ease-in-out;
}

/* Animation for fade-in */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}


.services-header, .generalinfo{
  text-align: center;
  align-items: center;
}

.generaldiv{
  text-align: center;
  align-items: center;
  border-radius: 20px;
  background: linear-gradient(to right bottom,rgb(228, 29, 228),aqua);
  box-shadow: 0 0 30px rgb(119, 255, 0);
  border: 2px solid aquamarine;
  margin: 50px 10px 70px;
  padding: 0;
}

@media screen and (max-width: 700px) {
  .services-container {
    flex-direction: column-reverse;
    justify-content: center;
    align-items: center;
  }

  .sidebar {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    top: 0;
    border-right: none;
    order: 2;
    padding: 0;
    margin: -180px 0 0 0;
    width: 100%;
    height: 40%;
    background-color: white;
    border-bottom: 2px solid lightgray;
  }

  .sidebar ul {
    text-align: center;
    margin: 0 0 -170px 0;
    padding: 10px 0 0 0;
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 95%;
  }

  .sidebar ul li{
    margin: 0;
    padding: 0;
    font-size: .7rem;
  }
  .sidebar ul .li1,   .sidebar ul .li2 {
    margin: 10px 0;
    padding-right: 15px;
    border-right: 2px solid darkgrey;
  }

  .content-container {
    flex: 1;
    max-width: 100%;
    margin: 0;
    padding-bottom: 0;
  }
}
