.navbar {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    background-color: rgb(235, 10, 235);
    border-bottom: 3px solid aqua;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2px 50px;
    position: relative;
    z-index: 1000;
}

.nav-list {
    border-bottom: none;
    list-style: none;
    display: flex;
    align-items: center;
    gap: 50px;
    margin: 0 30px 0 0;
    padding: 0;
}

.logo {
    margin: 0;
}

.nav-list li {
    margin: 0;
    padding: 2px 7px 4px;
    transition: 0.5s all ease-in-out;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.nav-list .link {
    text-decoration: none;
    color: white;
    font-weight: bold;
    font-size: 14px;
}

.nav-list i{
    margin-right: 5px;
    color: white;
}

.nav-list .img1{
    height: 20px;
    width: 20px;
    margin-right: 5px;
    border-radius: 10px;
    background: transparent;
}

.nav-list .link.dark-mode{
    color: white;
}

.nav-list li:hover,
.nav-list li.active {
    background-color: #ff9900;
    box-shadow: 0 0 20px rgb(83, 191, 245);
    border-radius: 5px;
    transform: scale(1.1);
    transition: all 0.5s;
}

.nav-list li:hover.dark-mode,
.nav-list li.active.dark-mode {
    background-color: rgb(83, 191, 245);
    box-shadow: 0 0 20px rgb(249, 4, 249);
    color: white !important;
    border-radius: 5px;
    transform: scale(1.1);
    transition: all 0.5s;
}

.nav2 {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 40px;
}

.dark-mode-toggle {
    margin-left: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
}

.switch {
    position: fixed;
    top: 25px;
    right: 45px;
    transform: translate(-50%,-50%);
    display: inline-block;
    width: 40px;
    height: 24px;
}

.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.toggle {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #c6c4c4;
    -webkit-transition: .5s;
    transition: .5s;
    border-radius: 34px;
}

.fa-moon:before,
.fa-sun:before {
    position: absolute;
    left: 5px;
    bottom: 2px;
    height: 18px;
    width: 18px;
    font-size: 17px;
    -webkit-transition: .5s;
    transition: .5s;
    border-radius: 50%;
}

.fa-sun:before {
    position: absolute;
    left: 20px;
    bottom: 1.5px;
    font-size: 15px;
    color: white;
}

input:checked+.fa-solid {
    background-color: black;
}

input:checked+.fa-moon:before {
    -webkit-transform: translateX(16px);
    -ms-transform: translateX(16px);
    transform: translateX(16px);
}

.navbar.dark-mode {
    background-color: #333;
    border-bottom: 3px solid rgb(235, 10, 235);
    color: white;
    transition: 0.5s all ease-in-out;
}

.nav-list.dark-mode {
    border-bottom: none;
}

#check:checked~.nav-list {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding-top: 50px;
    width: 50%;
}

#check,
.checked {
    position: fixed;
    top: 25px;
    left: 20px;
    transform: translate(-50%,-50%);
    font-size: larger;
    display: none;
    color: black;
}

#check,
.checked.dark-mode{
    color: white;
}

.fa-user {
    font-size: x-large;
}

.user {
    position: fixed;
    top: 25px;
    right: 10px;
    transform: translate(-50%,-50%);
}

.nav2 .dark-mode-toggle .signin{
    color: white;
    border: none;
    background-color: transparent;
    font-weight: bold;
}

.nav2 .dark-mode-toggle .signout{
    font-size: small;
    position: absolute;
    top: 220px;
    right:  100px;
    padding: 5px 10px;
    border-radius: 10px;
    width: 100px;
    color: black;
    border: 2px solid lightgray;
    background-color: transparent;
}

.signout.dark-mode,
.signout:hover.dark-mode {
    color: #fff;
    border: 2px solid lightgray;
}

.fa-right-from-bracket {
    padding-right: 5px;
}

.user-info button {
    background-color: transparent;
    color: black;
    font-weight: normal;
}

.signin p,
.signout p {
    font-size: medium;
    padding-left: 5px;
}

.user-profile {
    position: relative;
    cursor: pointer;
}

.user-profile-pic-container {
    width: 30px;
    height: 30px;
    background-color: #ccc;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 24px;
    border-radius: 50%;
    color: #fff;
}

.user-profile-pic,
.fallback-profile-pic {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 50%;
}

.user-info {
    text-align: center;
    position: absolute;
    top: 30px;
    right: -140px;
    transform: translateX(-50%);
    background-color: #fff;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
    padding: 20px 40px 60px;
    border-radius: 10px;
    display: none;
}

.user-info.dark-mode {
    background-color: #858585;
    box-shadow: 0 0 20px rgba(252, 251, 251, 0.1);
    padding: 20px 40px 60px;
    border-radius: 10px;
    display: none;
}

.user-info-img {
    border-bottom: 2px solid darkgray;
}

.user-info img,
.fallback-info-img {
    width: 80px;
    height: 80px;
    overflow: hidden;
    border-radius: 50%;
    margin-bottom: 10px;
}

.fallback-info-img {
    width: 100px;
    height: 100px;
    background-color: #ccc;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 24px;
    border-radius: 50%;
    color: #fff;
}

.fallback-info-img .fallback-profile-pic {
    text-align: center;
    line-height: 100px;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 50%;
}

.user-info h4 {
    margin: 5px 0;
}

.user-info p {
    margin: 5px 0;
    font-weight: normal;
}

.user-profile:hover .user-info {
    display: block;
}
  

@media (max-width: 760px) {
    .navbar {
        justify-content: space-between;
        align-items: center;
        padding: 7px 10px 10px 30px;
    }

    .nav-list {
        position: fixed;
        top: 0;
        left: -100%;
        height: 100vh;
        width: 50%;
        margin-top: -3.5px;
        background: linear-gradient(rgb(204, 0, 255), yellow);
        flex-direction: column;
        justify-content: start;
        align-items: center;
        transition: all 1s;
        z-index: -1;
    }

    .nav-list.dark-mode {
        background: linear-gradient(black, grey);
    }

    .checked .fa-bars, .checked .fa-times{
        cursor: pointer;
        transition: all 1s;
    }

    .checked .fa-times.active{
        transform: rotate(90deg);
        transition: all 1s;
    }

    .nav-list li{
        margin: 20x;
        text-align: left;
    }

    .nav-list li:nth-child(1) {
        margin-top: 140px;
    }

    .nav-list li, .nav-list .link {
        font-size: .8rem;
    }

    .nav-list .link{
        margin-left: 5px;
    }

    .checked {
        display: block;
        z-index: 100;
        font-size: 1.4rem;
    }

    .menu-open.active {
        left: 0;
    }

    .dark-mode-toggle {
        gap: 10px;
        position: absolute;
        right: 10px;
    }

    .switch {
        position: fixed;
        top: 25px;
        right: 40px;
        transform: translate(-50%,-50%);
        display: inline-block;
        width: 40px;
        height: 24px;
    }

    .user {
        position: fixed;
        top: 25px;
        right: -10px;
        transform: translate(-50%,-50%);
    }

    .signin {
        font-size: small;
        margin-top: 4px;
    }

    .nav2 .dark-mode-toggle .signout{
        position: absolute;
        top: 210px;
        right: 85px;
        color: black;
        background-color: transparent;
    }

    .user-info {
        position: absolute;
        top: 40px;
        right: -100px;
    }

    .user-profile-pic-container {
        width: 40px;
        height: 40px;
    }
}